* {
  margin: 0;
  padding: 0;
}

.text {
  position: absolute;
  top: 3%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  list-style: none;
  border-bottom: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow:ellipsis;
}

.text-mobile {
  position: absolute;
  top: 10%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  list-style: none;
  border-bottom: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow:ellipsis;
}

.text.hidden {
  border-bottom: 1px solid #fff;
}
