.item{
    border: 1px solid #2C3E50;
    border-radius: 8px;
    width: 302px;
    height: 176.4px;
    margin: 1em;
    position: relative;
    cursor: pointer;
}

.tech{
    background-color: #2C3E50;
    width: 100%;
    height: 20%;
    position: absolute;
    bottom: 0px;
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
}
.tech p{
    position: absolute;
    font-size: 15px;
    color: white;
    left: 5%;
    line-height: 35px;
}
.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 100%;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
  }

.opacity {
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
  }
  .info {
  
    color: black;
    font: bold;
    font-size: 20px;
  }

  .item:hover .image {
    opacity: 0.3;
  }
  
  .item:hover .opacity {
    opacity: 1;
  }
  
